.footer{
    background-color: $color-gray-light;
    box-shadow: inset 0px 4px 4px rgba($color-black, .25);
    &__container{
        display: flex;
        align-items: center;
        padding-top: 40px;
        padding-bottom: 40px;
    }
    &__logo{
        flex: 1 0 auto;
    }
    &__phone{
        margin-right: 55px;
    }
    &__btn{
        border: 3px solid $color-green;
        border-radius: 40px;
        padding: 15px 30px;
        font-weight: 500;
        font-size: 25px;
        line-height: 29/25 * 100%;
    }
}