.benefits{
    margin-bottom: 100px;
    &__title{
        max-width: 1240px;
        margin-bottom: 120px;
        font-weight: 500;
        &--bold{
            font-weight: 700;
            color: $color-green;
        }
    }
    &__inner{
        padding-bottom: 40px;
        background-color: $color-green;
    }
    &__list{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        list-style: none;
        margin: 0;
        padding: 0;
    }
    @media(max-width: 1279.8px){

    }
    @media(max-width: 1139.8px){
        margin-bottom: 70px;
        &__list{
            justify-content: center;
            align-items: center;
        }
    }
    @media(max-width: 767.8px){
        margin-bottom: 50px;
    }
}
.benefits-item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 230px;
    margin-top: -80px;
    text-align: center;
    &:not(:nth-child(3n)){
        margin-right: 170px;
    }
    &:nth-child(-n+3){
        margin-bottom: 165px;
    }
    &__circle{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 160px;
        height: 160px;
        border-radius: 50%;
        margin-bottom: 25px;
        background-color: $color-white;
    }
    &__descr{
        margin: 0;
        font-weight: 700;
        font-size: 18px;
        line-height: 21/18 * 100%;
        color: $color-white;
    }
    @media(max-width: 1139.8px){
        &:not(:nth-child(3n)){
            margin-right: 0
        }
        &:nth-child(-n+3){
            margin-bottom: 0
        }
        &:not(:nth-child(2n)){
            margin-right: 200px;
        }
        &:nth-child(-n+2){
            margin-bottom: 165px;
        }
        &:nth-child(-n+4){
            margin-bottom: 165px;
        }
    }
    @media(max-width: 767.8px){
        &:not(:nth-child(2n)){
            margin-right: 70px;
        }
    }
    @media(max-width: 575.8px){
        &:not(:last-child){
            margin-bottom: 120px !important;
        }
        &:nth-child(-n+2){
            margin-bottom: 0;
        }
        &:nth-child(-n+4){
            margin-bottom: 0;
        }
        &:not(:nth-child(2n)){
            margin-right: 0;
        }
    }
}