.hero{
    margin-bottom: 100px;
    &__container{
        background-image: url(../img/hero-bg.png);
        background-position: 100% 100%;
        background-size: contain;
        background-repeat: no-repeat;
    }
    &__sub-title{
        margin: 0;
        margin-bottom: 35px;
        font-weight: 700;
        font-size: 30px;
        line-height: 35/30 * 100%;
        color: $color-green;
    }
    &__title{
        max-width: 600px;
        margin: 0;
        margin-bottom: 35px;
        font-weight: 900;
        font-size: 50px;
        line-height: 59/50 * 100%;
    }
    &__descr{
        max-width: 600px;
        margin: 0;
        margin-bottom: 60px;
        font-style: italic;
        font-size: 25px;
        line-height: 29/25 * 100%;
    }
    &__link{
        display: inline-block;
        padding: 15px 80px;
        background-color: $color-green;
        font-weight: 700;
        font-size: 18px;
        line-height: 21/18 * 100%;
        text-transform: uppercase;
        color: $color-white;
        transition: transform .3s ease 0s, box-shadow .3s ease 0s;
        &:hover{
            transform: scale(105%);
            box-shadow: 0 0 10px rgba($color-black, .4);
        }
    }
    @media(max-width: 1139.8px){
        margin-bottom: 70px;
        &__container{
            background-size: 50%;
        }
    }
    @media(max-width: 991.8px){
        &__sub-title{
            margin-bottom: 25px;
            font-size: 25px;
        }
        &__title{
            margin-bottom: 25px;
            max-width: 500px;
            font-size: 40px;
        }
        &__descr{
            margin-bottom: 40px;
            max-width: 400px;
            font-size: 18px;
        }
        &__link{
            padding: 12px 60px;
            font-size: 16px;
        }
    }
    @media(max-width: 767.8px){
        margin-bottom: 50px;
        &__container{
            background-size: 45%;
        }
        &__sub-title{
            margin-bottom: 20px;
            font-size: 18px;
        }
        &__title{
            margin-bottom: 15px;
            font-size: 35px;
        }
        &__descr{
            max-width: 320px;
            margin-bottom: 30px;
            font-size: 16px;
        }
        &__link{
            padding: 10px 40px;
            font-size: 16px;
        }
    }
    @media(max-width: 575.8px){
        &__sub-title{
            font-size: 14px;
        }
        &__title{
            max-width: 350px;
            margin-bottom: 10px;
            font-size: 30px;
        }
        &__descr{
            font-size: 14px;
        }
    }
    @media(max-width: 479.8px){
        &__sub-title{
            max-width: 220px;
            font-size: 13px;
        }
        &__title{
            max-width: 290px;
            font-size: 25px;
        }
        &__descr{
            max-width: 250px;
            font-size: 12px;
        }
        &__link{
            font-size: 14px;
        }
    }
    @media(max-width: 374.8px){
        &__container{
            background-size: 40%;
        }
        &__sub-title{
            margin-bottom: 10px;
        }
        &__title{
            max-width: 100%;
            font-size: 21px;
        }
        &__link{
            padding: 10px 25px;
        }
    }
}