.services{
    margin-bottom: 100px;
    &__title{
        margin-bottom: 10px;
        font-weight: 700;
    }
    &__sub-title{
        margin: 0;
        margin-bottom: 65px;
        font-style: italic;
        font-size: 30px;
        line-height: 35/30 * 100%;
    }
    &__list{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        list-style: none;
        margin: 0;
        padding: 0;
    }
    @media(max-width: 1139.8px){
        margin-bottom: 70px;
    }
    @media(max-width: 992.8px){
        margin-bottom: 20px;
    }
    @media(max-width: 767.8px){
        margin-bottom: 0;
        &__list{
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }
}
.services-item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 345px;
    padding: 30px 20px;
    text-align: center;
    transition: transform .3s ease 0s, box-shadow .3s ease 0s;
    &:hover{
        transform: translateY(-5px);
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    }
    &__img{
        margin-bottom: 20px;
    }
    &__title{
        margin: 0;
        margin-bottom: 20px;
        font-weight: 500;
        font-size: 30px;
        line-height: 35/30 * 100%;
    }
    &__descr{
        margin: 0;
        font-size: 20px;
        line-height: 23/20 * 100%;
    }
    @media(max-width: 1139.8px){
        margin-bottom: 50px;
    }
    @media(max-width: 991.8px){
        margin-bottom: 50px;
        &__title{
            font-size: 25px;
        }
        &__descr{
            font-size: 18px;
        }
    }
    @media(max-width: 479.8px){
        max-width: 290px;
        padding: 15px;
        &__title{
            font-size: 18px;
        }
        &__descr{
            font-size: 14px;
        }
    }
    @media(max-width: 374.8px){
        max-width: 260px;
        padding: 10px;
    }
}