html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background-color: #fff;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
}

img {
  display: block;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.container {
  width: 100%;
  max-width: 1450px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn-reset {
  border: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.title-default {
  margin: 0;
  font-size: 45px;
  line-height: 117.77778%;
}

.phone {
  font-weight: 700;
  font-size: 20px;
  line-height: 115%;
  color: inherit;
  transition: color .3s ease 0s;
}

.phone:hover {
  color: #00a155;
}

@media (max-width: 991.8px) {
  .phone {
    font-size: 16px;
  }
}

@media (max-width: 767.8px) {
  .phone {
    display: none;
  }
}

.header {
  margin-bottom: 100px;
}

.header__container {
  display: flex;
  align-items: center;
  padding-top: 60px;
  padding-bottom: 60px;
}

.header__logo {
  flex: 1 0 auto;
}

@media (max-width: 1139.8px) {
  .header {
    margin-bottom: 70px;
  }
  .header__container {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

@media (max-width: 767.8px) {
  .header {
    margin-bottom: 50px;
  }
  .header__container {
    padding-top: 25px;
    padding-bottom: 25px;
  }
}

.logo__img {
  width: 100%;
  max-width: 229px;
  height: auto;
}

@media (max-width: 575.8px) {
  .logo__img {
    max-width: 200px;
  }
}

@media (max-width: 479.8px) {
  .logo__img {
    max-width: 180px;
  }
}

.menu {
  margin-right: 120px;
}

.menu__list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu__item:not(:last-child) {
  margin-right: 60px;
}

.menu__link {
  font-weight: 700;
  font-size: 20px;
  line-height: 115%;
  color: inherit;
  transition: color .3s ease 0s;
}

.menu__link:hover {
  color: #00a155;
}

@media (max-width: 1139.8px) {
  .menu {
    margin-right: 80px;
  }
  .menu__item:not(:last-child) {
