html{
    box-sizing: border-box;
}
*,
*::before,
*::after{
    box-sizing: inherit;
}
body{
    margin: 0;
    background-color: $color-white;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
}
img{
    display: block;
    max-width: 100%;
    object-fit: cover;
}
a{
    text-decoration: none;
}
.container{
    width: 100%;
    max-width: 1450px;
    margin: 0 auto;
    padding: 0 15px;
}
.btn-reset{
    border: 0;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
}
.title-default{
    margin: 0;
    font-size: 45px;
    line-height: 53/45 * 100%;
}
.phone{
    font-weight: 700;
    font-size: 20px;
    line-height: 23/20 * 100%;
    color: inherit;
    transition: color .3s ease 0s;
    &:hover{
        color: $color-green;
    }
    @media(max-width: 991.8px){
        font-size: 16px;
    }
    @media(max-width: 767.8px){
        display: none;
    }
}