.header{
    margin-bottom: 100px;
    &__container{
        display: flex;
        align-items: center;
        padding-top: 60px;
        padding-bottom: 60px;
    }
    &__logo{
        flex: 1 0 auto;
    }
    @media(max-width: 1139.8px){
        margin-bottom: 70px;
        &__container{
            padding-top: 40px;
            padding-bottom: 40px;
        }
    }
    @media(max-width: 767.8px){
        margin-bottom: 50px;
        &__container{
            padding-top: 25px;
            padding-bottom: 25px;
        }
    }
}
.logo{
    &__img{
        width: 100%;
        max-width: 229px;
        height: auto;
    }
    @media(max-width: 575.8px){
        &__img{
            max-width: 200px;
        }
    }
    @media(max-width: 479.8px){
        &__img{
            max-width: 180px;
        }
    }
}
.menu{
    margin-right: 120px;
    &__list{
        display: flex;
        list-style: none;
        margin: 0;
        padding: 0;
    }
    &__item{
        &:not(:last-child){
            margin-right: 60px;
        }
    }
    &__link{
        font-weight: 700;
        font-size: 20px;
        line-height: 23/20 * 100%;
        color: inherit;
        transition: color .3s ease 0s;
        &:hover{
            color: $color-green;
        }
    }
    @media(max-width: 1139.8px){
        margin-right: 80px;
        &__item{
            &:not(:last-child){
                margin-right: 40px;
            }
        }
    }
    @media(max-width: 991.8px){
        margin-right: 40px;
        &__item{
            &:not(:last-child){
                margin-right: 15px;
            }
        }
        &__link{
            font-size: 16px;
        }
    }
    @media(max-width: 767.8px){
        display: none;
    }
}
.icon-menu{
    display: none;
}