.advice{
    margin-bottom: 100px;
    &__inner{
        padding: 40px 140px;
        background-color: $color-gray-light;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
    &__title{
        margin: 0;
        margin-bottom: 35px;
        font-weight: 700;
        font-size: 30px;
        line-height: 35/30 * 100%;
        text-align: center;
    }
    @media(max-width: 1239.8px){
        &__inner{
            padding: 40px 80px;
        }
    }
    @media(max-width: 1139.8px){
        margin-bottom: 70px;
        &__inner{
            padding: 40px;
        }
    }
    @media(max-width: 991.8px){
        &__title{
            font-size: 25px;
        }
    }
    @media(max-width: 767.8px){
        margin-bottom: 50px;
        &__title{
            margin-bottom: 25px;
            font-size: 21px;
        }
    }
    @media(max-width: 575.8px){
        &__title{
            font-size: 18px;
            text-align: left;
        }
    }
    @media(max-width: 374.8px){
        &__inner{
            padding: 20px;
        }
        &__title{
            font-size: 16px;
            text-align: left;
        }
    }
}
.advice-form{
    display: flex;
    flex-direction: column;
    &__main{
        display: flex;
        align-items: center;
        margin-bottom: 30px;
    }
    &__inner{
        flex: 1 0;
    }
    &__input{
        width: 270px;
        height: 50px;
        border: 0;
        padding: 0 10px;
        background-color: $color-white;
        font-style: italic;
        font-weight: 300;
        font-size: 18px;
        line-height: 21/18 * 100%;
        text-align: center;
        color: $color-black;
        &:not(:last-child){
            margin-right: 30px;
        }
        &::placeholder{
            font-style: italic;
            font-weight: 300;
            font-size: 18px;
            line-height: 21/18 * 100%;
            color: $color-gray;
        }
    }
    &__descr{
        margin: 0;
        font-style: italic;
        font-size: 15px;
        line-height: 18/15 * 100%;
        color: $color-gray;
    }
    &__btn{
        padding: 15px 30px;
        background-color: $color-green;
        font-weight: 700;
        font-size: 18px;
        line-height: 21/18 * 100%;
        color: $color-white;
        transition: transform .3s ease 0s, box-shadow .3s ease 0s;
        &:hover{
            transform: scale(105%);
            box-shadow: 0 0 10px rgba($color-black, .4);
        }
    }
    @media(max-width: 991.8px){
        &__inner{
            flex-direction: column;
        }
        &__input{
            display: block;
            &::placeholder{
                font-size: 16px;
            }
            &:not(:last-child){
                margin-right: 0;
                margin-bottom: 20px;
            }
        }
        &__btn{
            padding: 15px 20px;
            font-size: 16px;
        }
        &__descr{
            font-size: 16px;
        }
    }
    @media(max-width: 767.8px){
        &__main{
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        &__input{
            width: 290px;
            border-radius: 10px;
            margin-bottom: 20px;
            &::placeholder{
                font-size: 14px;
            }
        }
        &__btn{
            width: 290px;
            border-radius: 10px;
        }
        &__descr{
            font-size: 14px;
            text-align: center;
        }
    }
    @media(max-width: 479.8px){
        &__btn{
            font-size: 14px;
        }
    }
    @media(max-width: 374.8px){
        &__input{
            width: 230px;
        }
        &__btn{
            width: 230px;
        }
    }
}