.contacts{
    margin-bottom: 70px;
    padding-top: 100px;
    &__title{
        margin-bottom: 70px;
        font-weight: 500;
        &--bold{
            font-weight: 700;
            color: $color-green;
        }
    }
    &__inner{
        width: 100%;
        max-width: 710px;
        border-radius: 50px;
        padding: 40px;
        background-color: $color-green;
        box-shadow: 0px 4px 4px rgba($color-black, .25);
    }
}
.contacts-item{
    &--other{
        display: flex;
        justify-content: space-between;
    }
    &:not(:last-child){
        margin-bottom: 60px;
    }
    &__title{
        margin: 0;
        margin-bottom: 5px;
        font-weight: 700;
        font-size: 25px;
        line-height: 29/25 * 100%;
        text-transform: uppercase;
        color: $color-white;
    }
    &__descr{
        margin: 0;
        font-weight: 500;
        font-size: 25px;
        line-height: 29/25 * 100%;
        color: $color-white;
        &--link{
            color: $color-white
        }
    }
}